import { useState } from 'react'
import { Typography, ConfigProvider, Button, Input } from 'antd'
import { v4 as uuidv4 } from 'uuid'
import ListKey from './ListKey'

const { Title, Text, Paragraph } = Typography

const FragmentKey = () => {

    const list = [
        { id: uuidv4(), title: 'React' },
        { id: uuidv4(), title: 'JavaScript' },
        { id: uuidv4(), title: 'HTML/CSS' }]

    const [listState, setListState] = useState(list)

    const [inputText, setInputText] = useState('')

    const changeList = () => {

        setListState([...listState, { id: uuidv4(), title: inputText }])
        setInputText('')

    }
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                },
                token: {
                    colorLink: '#0e101c',
                    colorPrimary: '#0e101c'
                }
            }}>
            <Title className='title'>  Fragment  key</Title>
            <Paragraph>
                <Title>Fragment</Title>
                <Paragraph>
                                 DOM.
                      React.Fragment   ,   Fragment:
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                    import React, { Fragment } from 'react';

                    //   div  Fragment:

                    const ChildComponent = () => <Fragment>
                        <h1>Hello Child Component</h1>
                        <h1>Hello Child Component</h1>
                    </Fragment>
                    `}
                    </Text>
                </pre>
                <Paragraph>
                     (  ) -   :  ,  className  key:
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                        import React, { Fragment } from 'react';

                        //  

                        const ChildComponent = () => <>
                            <h1>Hello Child Component</h1>
                            <h1>Hello Child Component</h1>
                        </>
                        
                        //  !!!  
                        
                        const ChildComponent = () => < className="child" >
                            <h1>Hello Child Component</h1>
                            <h1>Hello Child Component</h1>
                        </>
                        `}
                    </Text>
                </pre>
            </Paragraph>
            <Paragraph>
                <Title>key</Title>
                <Paragraph>
                      React ,    ,   .  ,
                     React       :
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                        const numbers = [1, 2, 3, 4, 5];
                        const listItems = numbers.map((number) => <li key={number}>
                                {number}
                            </li>
                        );
                        `}
                    </Text>
                </pre>
                <Paragraph>
                           ,         . , ID   .
                      ID,    uuid:
                         .   ,   :
                    Warning: Each child in an array or iterator should have a unique key prop.     ,
                          .
                </Paragraph>
                <Paragraph>
                     key  reconciliation        VDOM. -        ,    .
                </Paragraph>
                <Paragraph>
                            ,   key   
                    <Button type='text' href='https://www.npmjs.com/package/uuid'>UUID</Button>.
                </Paragraph>
                <Paragraph>
                        ,          . ,        :
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                        function App() {

                            const list = [
                              { id: uuidv4(), title: 'React' },
                              { id: uuidv4(), title: 'JavaScript' },
                              { id: uuidv4(), title: 'HTML/CSS' }]
                          
                            const [listState, setListState] = useState(list)
                          
                            const [inputText, setInputText] = useState('')

                            const changeList = () => {

                                setListState([...listState, { id: uuidv4(), title: inputText }])
                            
                              }
                              return (
                                <div className='App'>
                                  <header className='App-header'>
                                    <List list={listState} setListState={setListState} />
                                    <input
                                      value={inputText}
                                      onKeyDown={(event) => event.key === 'Enter' && changeList()}
                                      onChange={(event) => setInputText(event.target.value)}
                                    />
                                    <button onClick={() => setListState(list)}></button>
                                  </header>
                            
                                </div>
                              );
                            }
                            
                            export default App;
                        `}
                    </Text>
                </pre>
                <Paragraph>
                          List         ,      .
                       List       ,   ,          :
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                        const List = ({list, setListState}) => {
    
                            const changeEl = (id) => {
                        
                                setListState(list.map(el => (el.id === id? {...el, title: '!!!{el.title}'} : el)))
                                
                            }
                        
                            return <ul>
                                {list.map(item => {
                                    return (
                                    <Fragment>
                                        <li key={item.id}>
                                            {item.title}
                                            <button onClick={() => changeEl(item.id)}>
                                                 
                                            </button>
                                        </li>
                                    </Fragment>)
                                })}
                            </ul>
                        }
                        
                        export default List;
                        `}
                    </Text>
                </pre>
                <Paragraph>
                         :
                </Paragraph>
                <ListKey list={listState} setListState={setListState} />
                <Input
                    style={{
                        width: 400
                    }}
                    value={inputText}
                    onKeyDown={(event) => event.key === 'Enter' && changeList()}
                    onChange={(event) => setInputText(event.target.value)}
                />
                <Button onClick={() => setListState(list)}></Button>
            </Paragraph>
        </ConfigProvider>
    )
}

export default FragmentKey;