import { Typography, List, ConfigProvider } from 'antd'

const { Title, Paragraph, Text } = Typography

const ContextComp = () => {
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                }
            }}>
            <Title className='title'> Context</Title>
            <Paragraph>
                <Paragraph>
                    Context                .
                      ,        ,  ""     .
                       ,        ,    ,       .
                              ,  .
                </Paragraph>
                <List
                    header={<Title level={3}> :</Title>}
                >
                    <List.Item>
                        <Paragraph>
                            1)	 :
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            import React from 'react';

                            const MyContext = React.createContext();
                            
                            export default MyContext;
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            2)	  
                                  -,    ,     
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            import React from 'react';
                            import MyContext from './MyContext';
                            import ComponentA from './ComponentA';
                            
                            const App = () => {
                              const value = 'Hello, Context!';
                            
                              return (
                                <MyContext.Provider value={value}>
                                  <ComponentA />
                                </MyContext.Provider>
                              );
                            }
                            
                            export default App;
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            3)	  useContext
                                  ,      
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            import React, { useContext } from 'react';
                            import MyContext from './MyContext';
                            import ComponentB from './ComponentB';
                            
                            function ComponentA() {
                              const value = useContext(MyContext);
                            
                              return (
                                <div>
                                  <h2>Component A</h2>
                                  <p>Received value from Context: {value}</p>
                                  <ComponentB />
                                </div>
                              );
                            }
                            
                            export default ComponentA;
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            4)	   (  )
                              ,   MyContext.Consumer
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            import React from 'react';
                            import MyContext from './MyContext';
                            import ComponentB from './ComponentB';
                            
                            function ComponentA() {
                              return (
                                <div>
                                  <h2>Component A</h2>
                                  <MyContext.Consumer>
                                    {(value) => (
                                      <p>Received value from Context: {value}</p>
                                    )}
                                  </MyContext.Consumer>
                                  <ComponentB />
                                </div>
                              );
                            }
                            
                            export default ComponentA;
                            `}
                        </Text>
                    </pre>
                </List>
                <Paragraph>
                               .
                </Paragraph>
            </Paragraph>
        </ConfigProvider>
    )
}

export default ContextComp;