import { Typography, List, ConfigProvider } from 'antd'

const { Title, Paragraph, Text } = Typography;

const LifeCycleClass = () => {
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                }
            }}
        >
            <Title className='title'>   </Title>
            <Paragraph>
                <List>
                    <List.Item>
                        <Paragraph>
                            1. Mounting ():
                        </Paragraph>
                    </List.Item>
                    <Paragraph>
                        - constructor():     .       .
                    </Paragraph>
                    <Paragraph>
                        - render();     React .
                    </Paragraph>
                    <Paragraph>
                        - componentDidMount():         DOM.      ,       .
                    </Paragraph>
                    <List.Item>
                        <Paragraph>
                            2. Updating ():
                        </Paragraph>
                    </List.Item>
                    <Paragraph>
                        - shouldComponentUpdate(nextProps, nextState):    .        false,    .
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                        shouldComponentUpdate (nextProps, nextState) {
                            return nextProps.name !== this.props.name
                        }`}
                        </Text>
                    </pre>
                    <Paragraph>
                        - render():     React .
                    </Paragraph>
                    <Paragraph>
                        - componentDidUpdate(prevProps, prevState):       .        , ,         .
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                        componentDidUpdate(prevProps, prevState) {
                            if (this.props.userID !== prevProps.userID) {
                                this.fetchData(this.props.userID);
                            }
                        }
                        `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            3. Unmounting ():
                        </Paragraph>
                    </List.Item>
                    <Paragraph>
                        - componentWillUnmount():      DOM.     ,      .
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                        class WillUnmountExample extends React.Component {
                            constructor (props) {
                            super(props)
                                this.state = {
                                    time: 0
                                }
                            }

                        componentDidMount () {
                            this.timerID = setInterval(() => {
                                this.setState(prevState => ({
                                    time: prevState.time + 1
                                }))
                            }, 1000)
                        }

                        componentWillUnmount () {
                            clearInterval(this.timerID)
                            alert('   DOM')
                        }

                        render () {
                            return (
                                <div>
                                    <p>: {this.state.time} </p>
                                </div>
                                )
                            }
                        }

                        export default WillUnmountExample
                        `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            4. Error Handling ( ):
                        </Paragraph>
                    </List.Item>
                    <Paragraph>
                        - componentDidCatch(error, info):       .      .
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                        import React, { Component } from 'react';

                        class ErrorBoundaryExample extends Component {
                          constructor(props) {
                            super(props);
                            this.state = {
                              hasError: false,
                              error: null,
                              errorInfo: null
                            };
                          }
                        
                          componentDidCatch(error, errorInfo) {
                            this.setState({
                              hasError: true,
                              error: error,
                              errorInfo: errorInfo
                            });
                          }
                        
                          render() {
                            if (this.state.hasError) {
                              return (
                                <div>
                                  <h2>-   .</h2>
                                  <p>{this.state.error && this.state.error.toString()}</p>
                                  <p> : {this.state.errorInfo && this.state.errorInfo.componentStack}</p>
                                </div>
                              );
                            }
                        
                            return this.props.children;
                          }
                        }
                        
                        class App extends Component {
                          render() {
                            return (
                              <div>
                                <h1>  ErrorBoundary</h1>
                                <ErrorBoundaryExample>
                                  {/*      */}
                                  <ComponentWithError />
                                </ErrorBoundaryExample>
                              </div>
                            );
                          }
                        }
                        
                        const ComponentWithError = () => {
                          const [throwError, setThrowError] = useState(false)
                        
                          const handleClick = () => setThrowError(presState => !presState.throwError)
                        
                          if (throwError) {
                            //  
                            throw new Error('    ComponentWithError')
                          }
                        
                          return (
                            <div>
                              <h3>   </h3>
                              <button onClick={() => handleClick()}> </button>
                            </div>
                          )
                        }
                        
                        export default App;
                        `}
                        </Text>
                    </pre>
                </List>
            </Paragraph>
        </ConfigProvider>
    )
}

export default LifeCycleClass;