import { Typography, Input, Button, List, ConfigProvider } from 'antd'
import { useState, useMemo, useRef } from 'react'

const { Title, Paragraph, Text } = Typography;

const RefsMemo = () => {
    const [count, setCount] = useState(0)

    const handleClick = useMemo(() => {
        console.log(count);
        return count % 2 === 0 ? count : count - 1
    }, [count])

    const reset = () => {
        setCount(0)
    }

    const textInput = useRef(null)
    const focus = () => {
        textInput.current.focus()
    }
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    },
                },
                token: {
                    colorPrimary: '#0e101c',
                }
            }}
        >
            <Title className='title'>  Refs  useMemo</Title>
            <Paragraph>
                <Title>Refs</Title>
                <Paragraph>
                    Refs     DOM-  React-,    `render()`.
                         :
                </Paragraph>
                <List>
                    <List.Item>
                        <Paragraph>
                            1.  ,  .
                        </Paragraph>
                    </List.Item>
                    <List.Item>
                        <Paragraph>
                            2.  .
                        </Paragraph>
                    </List.Item>
                    <List.Item>
                        <Paragraph>
                            3.    ,   DOM.
                        </Paragraph>
                    </List.Item>
                </List>
                <Paragraph>
                     :
                </Paragraph>
                <Input ref={textInput} style={{
                    width: 400
                }} />
                <Button onClick={focus}></Button>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                    const Test = () => {
                        const [text, setText] = useState('')
                        const textInput = useRef(null)
                        const valid = () => {
                            textInput.current.focus()
                        }
                        return (
                            <div>
                                <input ref={textInput} value={text} />
                                <button onClick={() => valid()}>
                                    Click to focus
                                </button>
                            </div>
                        )
                    }
                    `}
                    </Text>
                </pre>
                <Paragraph>
                    <Title>React.memo  useMemo</Title>
                    <Paragraph>
                        React.memo      (Higher Order Component)   React,     .
                               ,       .
                    </Paragraph>
                    <Paragraph>
                         ,        ,       .
                             ,            .
                    </Paragraph>
                    <Paragraph>
                        React.memo   ,            .
                           , React.memo    ,   .
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                            color: '#bf1650'
                        }}>
                            {`
                        const MyComponent = React.memo((props) => {
                            
                            //       

                            return (
                              <div>
                                {props.text}
                              </div>
                            );
                          });
                        `}
                        </Text>
                    </pre>
                    <Paragraph>
                        React.memo          (compareFunction),   ,
                            "".     , React.memo     (shallow comparison)   .
                    </Paragraph>
                    <Paragraph>
                        React  useMemo       .         ,
                           ()  .     ,   ,        .
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                            color: '#bf1650'
                        }}>
                            {`
                            const handleClick = useMemo(() => {
                                console.log(count);
                                return count % 2 === 0 ? count : count - 1
                            }, [count])
                        `}
                        </Text>
                    </pre>
                    <>
                        <Text>
                                : {handleClick}
                            <Button onClick={() => setCount(count + 1)}></Button>
                            <Button onClick={reset}></Button>
                        </Text>
                    </>
                </Paragraph>
            </Paragraph>
        </ConfigProvider>
    )
}

export default RefsMemo;