import { useState } from "react";
import { Typography, Button, List, ConfigProvider } from 'antd'

const { Text, Paragraph, Title } = Typography;

const State = () => {
    const [count, setCount] = useState(0);

    const increment = () => {
        setCount(count + 1);
    };

    const reset = () => {
        setCount(0)
    }

    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                },
                token: {
                    colorPrimary: '#0e101c'
                }
            }}
        >
            <Title className="title"> </Title>
            <Paragraph>
                <Paragraph>
                    State     JS,      .
                           state,   ,      .
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                    class Counter extends Component { 
                        constructor(props) {
                        super(props); 
                            this.state = { count: 0, }; 
                    }
                    `}
                    </Text>
                </pre>
                <Paragraph>
                            setState().         state !
                </Paragraph>
                <Paragraph>
                     2     :
                </Paragraph>
                <List>
                    <List.Item>
                        <Text code='true' style={{
                            color: '#bf1650'
                        }}>
                            {`this.setState({username:'Ilya'});`}
                        </Text>
                    </List.Item>
                    <List.Item>
                        <Text code='true' style={{
                            color: '#bf1650'
                        }}>
                            {`this.setState(() => ({ username: 'Ilya' }))`}
                        </Text>
                    </List.Item>
                </List>
                <Paragraph>
                            useState.        . ,       :
                </Paragraph>
                <Text code='true' style={{
                    color: '#bf1650'
                }}>
                    {`const [count, setCount] = useState(0) `}
                </Text>
                <Paragraph>
                         :
                </Paragraph>
                <Text>: {count}</Text>
            </Paragraph>
            <Button onClick={increment}></Button>
            <Button onClick={reset}></Button>
        </ConfigProvider>
    )
}

export default State;