import { Typography, List, ConfigProvider } from 'antd'

const { Title, Paragraph } = Typography;

const VDOMReact = () => {
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                }
            }}
        >
            <Title className='title'>Virtual DOM</Title>
            <Paragraph>
                VDOM      DOM   . React     ( HTML-),         DOM.    ,  ,       DOM, React   DOM,       ,   DOM.
                  VDOM   ,      DOM       (    ,     DOM        , React    ).
            </Paragraph>
            <Paragraph>
                Reconciliation   ,   React          ,      UI.
            </Paragraph>
            <List
                header={<Title level={3}>   :</Title>}
            >
                <List.Item>
                    <Paragraph>
                        1.          JSX-,    ()     
                    </Paragraph>
                </List.Item>
                <List.Item>
                    <Paragraph>
                        2.	React   DOM,    DOM  
                    </Paragraph>
                </List.Item>
                <List.Item>
                    <Paragraph>
                        3.	        , React   reconciliation       DOM
                    </Paragraph>
                </List.Item>
                <List.Item>
                    <Paragraph>
                        4.	      ,  React ,   /,     
                    </Paragraph>
                </List.Item>
                <List.Item>
                    <Paragraph>
                        5.	   , React       DOM
                    </Paragraph>
                </List.Item>
            </List>
        </ConfigProvider>
    )
}

export default VDOMReact;