import { Typography, List, ConfigProvider } from 'antd'

const { Title, Paragraph, Text } = Typography;

const HighOrderComponents = () => {
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                }
            }}
        >
            <Title className='title'>  </Title>
            <Paragraph>
                <Paragraph>
                    HOC     ,          .     ,
                     ,             .
                </Paragraph>
                <List>
                    <List.Item>
                        <Paragraph>
                            1.  : HOC     ,       ,
                                    .
                        </Paragraph>
                    </List.Item>
                    <List.Item>
                        <Paragraph>
                            2.   props: HOC     props .
                             , ,             .
                        </Paragraph>
                    </List.Item>
                    <List.Item>
                        <Paragraph>
                            3.  : HOC        , ,  ,     ..
                        </Paragraph>
                    </List.Item>
                </List>
                <Paragraph>
                        :
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                const withLogger = (WrappedComponent) => {
                    return (props) => {
                      console.log('Component rendered:', WrappedComponent.name);
                      return <WrappedComponent {...props} />;
                    };
                  };
                `}
                    </Text>
                </pre>
            </Paragraph>
        </ConfigProvider>
    )
}

export default HighOrderComponents;