import { Typography, List, ConfigProvider } from 'antd'

const { Title, Paragraph, Text } = Typography;

const EventsComp = () => {
  return (
    <ConfigProvider
      theme={{
        components: {
          Typography: {
            fontSize: 18,
            colorText: '#0e101c'
          }
        }
      }}
    >
      <Title className='title'>Events     </Title>
      <Paragraph>
        <Paragraph>
           React  (events)      .     ,   , ,    .
        </Paragraph>
        <List
          header={<Title level={3}>5    :</Title>}
        >
          <List.Item>
            <Paragraph>
              1. onClick ():
            </Paragraph>
          </List.Item>
          <Paragraph>
             onClick     .        React.
          </Paragraph>
          <pre>
            <Text code='true' style={{
              color: '#bf1650'
            }}>
              {`
                        const handleClick = () => {
                            return ''
                          }
                          
                          const MyComponent = () => {
                            return (
                              <button onClick={handleClick}>Press</button>
                            );
                          }
                        `}
            </Text>
          </pre>
          <List.Item>
            <Paragraph>
              2. onChange ()
            </Paragraph></List.Item>
          <Paragraph>
             onChange      ,     (input),  (checkbox)     (radio).
             React,  event  callback-   ,         . event.target.value    input .
          </Paragraph>
          <pre>
            <Text code='true' style={{
              color: '#bf1650'
            }}>
              {`
                        const [value, setValue] = useState('')
                        const handleChange = (event) => {
                            setValue(event.target.value);
                          }
                          
                          const MyComponent = () => {
                            return (
                              <input type="text" onChange={handleChange} value={value}/>
                            );
                          }
                        `}
            </Text>
          </pre>
          <List.Item>
            <Paragraph>
              3. onSubmit ( ):
            </Paragraph>
          </List.Item>
          <Paragraph>
             onSubmit    .    ,       .
          </Paragraph>
          <pre>
            <Text code='true' style={{
              color: '#bf1650'
            }}>
              {`
                          const handleSubmit = (event) => {
                            event.preventDefault();
                            console.log(' !');
                          }
                          
                          const MyComponent = () => {
                            return (
                              <form onSubmit={handleSubmit}>
                                {/*   */}
                                <button type="submit"></button>
                              </form>
                            );
                          `}
            </Text>
          </pre>
          <List.Item>
            <Paragraph>
              4. onMouseOver ( )
              :</Paragraph>
          </List.Item>
          <Paragraph>
             onMouseOver ,     .             .
          </Paragraph>
          <pre>
            <Text code='true' style={{
              color: '#bf1650'
            }}>
              {`
                        const handleMouseOver = () => {
                            console.log(' !');
                          }
                          
                          const MyComponent = () => {
                            return (
                              <div onMouseOver={handleMouseOver}>   </div>
                            );
                          }
                        `}
            </Text>
          </pre>
          <List.Item>
            <Paragraph>
              5. onKeyDown ( ):
            </Paragraph>
          </List.Item>
          <Paragraph>
             onKeyDown      .              .
          </Paragraph>
          <pre>
            <Text code='true' style={{
              color: '#bf1650'
            }}>
              {`
                        const handleKeyDown = (event) => {
                            console.log(' :', event.key);
                          }
                          
                          const MyComponent = () => {
                            return (
                              <input type="text" onKeyDown={handleKeyDown} />
                            );
                          }
                        `}
            </Text>
          </pre>
        </List>
      </Paragraph>
    </ConfigProvider>
  )
}

export default EventsComp;