import { Typography, ConfigProvider } from 'antd'

const { Title, Paragraph, Text } = Typography;

const Styling = () => {
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                }
            }}>
            <Title className='title'>  React</Title>
            <Paragraph>
                <Paragraph>
                      -     ,        .
                         Tailwind CSS  Styled Components:
                </Paragraph>
                <Title level={3}>Tailwind CSS</Title>
                <Paragraph>
                    Tailwind CSS   CSS-,     ,      .
                        ,       CSS-,      HTML-.
                           ,        .
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                const TailwindButton = () => {
                    return (
                      <button className="bg-blue-500 hover:bg-blue-700 font-semibold rounded">
                        Click me (Tailwind)
                      </button>
                    );
                  };
                `}
                    </Text>
                </pre>
                <Title level={3}>Styled Components</Title>
                <Paragraph>
                    Styled Components        React-.    CSS-  -,
                    Styled Components      JavaScript-      ( ).
                </Paragraph>
                <pre>
                    <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                        {`
                const StyledDiv = styled.div'
                background-color: blue;
                color: white;
                padding: 4px;
                ';
              
                const MyComponent = () => {
                    return (
                        <StyledDiv>
                                Styled Components.
                        </StyledDiv>
                    );
                }
                `}
                    </Text>
                </pre>
            </Paragraph>
        </ConfigProvider>
    )
}

export default Styling;