import { Typography, List, ConfigProvider } from 'antd'

const { Title, Text, Paragraph } = Typography;

const Routing = () => {
    return (
        <ConfigProvider
            theme={{
                components: {
                    Typography: {
                        fontSize: 18,
                        colorText: '#0e101c'
                    }
                }
            }}>
            <Title className='title'>React Routing</Title>
            <Paragraph>
                <Paragraph>
                      React            .
                             .
                    ..             ,       ,  ,
                        .
                </Paragraph>
                <List
                    header={<Title level={3}>  React       :</Title>}
                >
                    <List.Item>
                        <Paragraph>
                            1)	BrowserRouter - ,         , ..     
                        </Paragraph>
                    </List.Item>
                    <List.Item>
                        <Paragraph>
                            2)	Routes - -,       , ..      Route
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <Routes> 
                                <Route path="/" element={<Home />} /> 
                                <Route path="/about" element={<About />} />
                                <Route path="/contact" element={<Contact />} /> 
                            </Routes>
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            3)	Route - ,      Routes  Route   path  element
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <Routes> 
                                <Route path="/" element={<Home />} /> 
                                <Route path="/about" element={<About />} />
                                <Route path="/contact" element={<Contact />} /> 
                            </Routes>                         
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            4)	Link -          
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <Link to="/"></Link> 
                            <Link to="/about"> </Link>
                            <Link to="/contact"></Link>                            
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            5)	NavLink -     (   Link  ,  NavLink   active  ,    ).
                                     .
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <NavLink to="/" activeClassName="active"></NavLink>
                            <NavLink to="/about" activeClassName="active"> </NavLink>
                            <NavLink to="/contact" activeClassName="active"></NavLink>                            
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            6)	Outlet - ,      , :
                              Layout,    :
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <header></header>
                            <Outlet/>
                            <footer></footer>                            
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                        ,    App           :
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <Routes>
                                <Route path=/ element={<Layout/>}>
                                //    
                                </Route>
                            </Routes>
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            7)	useNavigate()   ,      .
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            const Home = () => { 
                                const navigate = useNavigate(); 
                                const handleClick = () => { 
                                    navigate('/about');
                                }; 
                                return ( 
                                    <div>
                                        <h1> </h1>
                                        <button onClick={handleClick}>    </button>
                                    </div> 
                                ); 
                                };
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                          navigate     :
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            const navigate = useNavigate()
                            const goBack = () => navigate(-1) 
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                           navigate(),      useNavigate()     (  ,     
                        ( )   ( )),   ,    URL-      ,
                                  Link  .
                    </Paragraph>
                    <Paragraph>
                               Navigate     :
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <Route path=/about element={<AboutPage/>}/>
                            <Route path=/about-us element={<Navigate to=/about/>}/>                            
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                                ,    /    ,
                                      NavigateOptions,   2 :
                    </Paragraph>
                    <Paragraph>
                        a)	replace    false,      ,       ,     true,       .
                    </Paragraph>
                    <Paragraph>
                        b)	state   
                    </Paragraph>
                    <Paragraph>
                          navigate       http-.
                    </Paragraph>
                    <List.Item>
                        <Paragraph>
                            8)	useParams()   ,      .
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            const UserProfile = () => { 
                                const params = useParams(); 
                                return ( 
                                    <div> 
                                        <h1> : {params.username}</h1>
                                    </div> 
                                ); 
                                };
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                                 params,      :username     
                             URL-, :
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            <Route path=about/:id element={<SinglePage/>}>
                            const { id } = useParams()
                            <Link to={'about/{id}/edit'}>Edit post</Link                            
                            `}
                        </Text>
                    </pre>
                    <List.Item>
                        <Paragraph>
                            9)	useLocation()   ,       URL   .
                        </Paragraph>
                    </List.Item>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            const Page = () => { 
                                const location = useLocation(); 
                                return ( 
                                    <div> 
                                        <h1> URL: {location.pathname}</h1>
                                    </div> 
                                ); 
                            }; 
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                         useLocation()   Location,      :
                    </Paragraph>
                    <Paragraph>
                        a)	hash    URL-
                    </Paragraph>
                    <Paragraph>
                        b)	key    
                    </Paragraph>
                    <Paragraph>
                        c)	pathname    URL- ( /about)
                    </Paragraph>
                    <Paragraph>
                        d)	search     URL-
                    </Paragraph>
                    <Paragraph>
                        e)	state    ,    Link state={ }  useNavigate ()
                        :
                    </Paragraph>
                    <pre>
                        <Text code='true' style={{
                        color: '#bf1650'
                    }}>
                            {`
                            const navigate = useNavigate()
                            const goBack = () => navigate(/about , {state: 123})                            
                            `}
                        </Text>
                    </pre>
                    <Paragraph>
                         ,     /about  state  location   123.
                    </Paragraph>
                </List>
            </Paragraph>
        </ConfigProvider>
    )
}

export default Routing;